.iconcheck {
  position: relative;
  width: 85px;
  height: 85px;
  font-size: 13px;
}
.checkframe {
  /* background-color: white; */
  color: white;
  position: absolute;
  top: 0px;
  left: 4px;
  border: var(--primaryBlue) 2px solid;
  border-radius: 5px;
  width: 16px;
  height: 16px;
}

.textcheckframe {
  /* background-color: white; */
  color: white;

  border: var(--primaryBlue) 2px solid;
  border-radius: 5px;
  width: 16px;
  height: 16px;
  margin-right: 10px;
  /* font-size: 16px; */
}
.textCheck {
  margin: auto 10px;
  font-size: 18px;
  font-weight: 600;
}
