body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --primary: #2478cc;
  --secondary: #bbdefb;
  --tertiary: #e3f2fd;

  --info: #0bc5ea;
  --success: #68d391;
  --warning: #f6e05e;
  --danger: #fc8181;

  --primaryBlue: #188cff;
  --primaryGrey: #ededed;
  --dark: #333;
  --light: #fff;
  --shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
}

html {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  /* text-align: center; */
  /* display: grid; */
  border: 1px solid var(--primaryGrey);
  width: 1200px;
  height: 900px;
  margin: auto;
  overflow: hidden;
  margin-bottom: 30px;
  position: relative;
}

/* Nav */
.nav {
  display: grid;
  grid-gap: 5px;
  padding: 20px;
  grid-template-columns: repeat(4, 1fr);
}

/* .container {
  margin: 30px 100px;
} */

.board {
  /* width: 300px; */
  height: 400px;
  border: solid 2px;
}

.card {
  background-color: black;
  color: white;
  padding: 1em;
  margin: 1em;
}

/*drag and drop*/
.dnd-container1 {
  display: grid;
  grid-template-columns: 26% auto;
  gap: 1em;
}
.dnd-container2 {
  display: grid;
  grid-template-columns: 25% 35% 35%;
  gap: 1em;
}
.draggers {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: 100px;
  gap: 1em;
}
.title {
  margin-left: 25px;
  /* font-size: 18px; */
}
.prehome {
  margin: 0px 20px;
  width: 700px;
  height: 200px;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 1em;
  border: solid 1px #bcbcbc;
  grid-auto-rows: 90px;
  padding: 20px;
  -webkit-box-shadow: 0 0 5px 2px rgb(188, 188, 188, 0.5);
  -moz-box-shadow: 0 0 5px 2px rgb(188, 188, 188, 0.5);
  box-shadow: 0 0 5px 2px rgb(188, 188, 188, 0.5);
  border-radius: 15px;
  /* margin-right: 30px; */
}
