.radio {
  position: absolute;
  /* top: 300px; */
}

.dragItem {
  position: relative;
  width: 85px;
  height: 85px;
  font-size: 13px;
}
.charText {
  font-size: 13px;
}

.iconClose {
  display: none;
  color: #949494;
  position: absolute;
  top: 0px;
  right: 3px;
}
.iconClose:hover {
  color: #606060;
}
.iconRemove {
  color: var(--primary);
}
.iconRemove:hover {
  color: var(--secondary);
}
.zone {
  width: 500px;
  height: 400px;
  border: 1px solid darkblue;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
